/**
 * levelConfig:
 * 0: |
 * 1: |-
 * 2: âŒž
 * 3: empty
 */
const fnConvertObjToArray = (obj = {}, { prefixKey, levelConfig } = {}) => {
  let _keys = Object.keys(obj)
  let _arr = []
  for (let i = 0; i < _keys.length; i++) {
    let k = _keys[i]
    let _value = obj[k]
    let _type = typeof _value
    if (_type == "object") {
      if (_value == null) {
        _type = "null"
      }
      if (Array.isArray(_value)) {
        _type = "array"
      }
    }
    let _id = `${prefixKey != null ? prefixKey + "." : ""}${k}`
    let _levelConfigCurrent = 5
    let _parentsLevelConfig = []
    if (levelConfig && levelConfig.length > 0) {
      _parentsLevelConfig = [...levelConfig]
      for (let i = 0; i < _parentsLevelConfig.length; i++) {
        if (_parentsLevelConfig[i] == 2 || _parentsLevelConfig[i] == 3) {
          _parentsLevelConfig[i] = 3 //emtpy
        } else {
          _parentsLevelConfig[i] = 0
        }
      }
      // for (let i = _parentsLevelConfig.length - 1; i >= 0; i--) {
      //   if (_parentsLevelConfig[i] == 2) {
      //     _parentsLevelConfig[i] = 3 //emtpy
      //   } else if (i < _parentsLevelConfig.length - 1) {
      //     //ong noi dua vao ba
      //     if (_parentsLevelConfig[i + 1] == 2) {
      //       _parentsLevelConfig[i] = 3 //emtpy
      //     } else {
      //       _parentsLevelConfig[i] = 0
      //     }
      //   } else {
      //     _parentsLevelConfig[i] = 0
      //   }
      // }
    }
    if (_type == "object") {
      _levelConfigCurrent = 1
      if (i == _keys.length - 1) {
        _levelConfigCurrent = 2
      }
    } else if (i == _keys.length - 1) {
      _levelConfigCurrent = 2
    } else {
      _levelConfigCurrent = 1
    }
    let _levelConfigAll = [..._parentsLevelConfig, _levelConfigCurrent]
    _arr.push({
      Id: _id,
      Key: k,
      Type: _type,
      Value: _value,
      TreeLevel: _id.split(".").length - 1,
      LastOfLevel: i == _keys.length - 1,
      HasChild: _type == "object" ? true : false,
      LevelConfigs: _levelConfigAll,
      UI_MergeRowByField: _type == "object" ? "Key" : null,
      UI_CellType_Value: _type == "boolean" ? "checkbox" : "textarea",
      UI_StyleRow: {
        display: "flex",
        alignItems: "center",
        textAlign: "left",
        justifyContent: "flex-start",
        paddingLeft: "5px",
        fontWeight: _type == "object" ? "bold" : "normal",
      },
    })
    if (_type == "object") {
      let _arrChild = fnConvertObjToArray(_value, {
        prefixKey: _id,
        levelConfig: _levelConfigAll,
      })
      if (_arrChild && _arrChild.length > 0) {
        _arr = _arr.concat(_arrChild)
      }
    }
  }
  return _arr
}
const ModalEditor = props => {
  const { ctc, obj, config, fnList, configFromModal } = props
  const [v, setV] = React.useState(obj)
  const [dataList, setDataList] = React.useState(fnConvertObjToArray(v))
  const [isLoading, setIsLoading] = React.useState(false)
  const canEdit = ctc.canEdit
  React.useEffect(() => {}, [])
  console.warn("render ModalEditor:", props)
  return (
    <WrapJsonModalContent>
      {MyModal.renderHeader({
        cModal: props.inMyModal,
        styleModalHeader: {
          marginBottom: "0.5rem",
        },
        title: props => {
          return (
            <>
              {configFromModal.title}
              {/* <div className="btn-group"></div>
              <div
                className="btn-group"
                style={{ position: "absolute", right: "2rem" }}
              ></div> */}
            </>
          )
        },
      })}
      <div
        style={{
          flex: 1,
          overflow: `auto`,
          minHeight: "100px",
        }}
      >
        <MyPage
          configPage={{
            UIUrl: "",
            ScreenCode: "PageNotFound",
            Title: "",
            APIName: "",
            UIType: "admin_table_client",
            Config: null,
            RequestData: "",
            Id: "",
          }}
          fnList={{
            [kfnGetIsInModal]: () => {
              return true
            },
            fnCustomUpdateField: ({
              apiController,
              cbSuccess,
              cbError,
              requestData,
              row,
              newValue,
              fieldName,
            }) => {
              console.warn("Update field", row, newValue, fieldName)
              if (row.Id) {
                LodashSet(v, row.Id, newValue)
                row.Value = newValue
                setDataList(fnConvertObjToArray(v))
                if (cbSuccess) {
                  cbSuccess()
                }
                console.warn("v", v)
                let _canSave = true
                if (_canSave) {
                  setIsLoading(true)
                  HUtils.runFuntion(fnList, "updateCell", [
                    HJson.getString(v),
                    {
                      cbSuccess: r => {
                        console.warn("cbSuccess:", r)
                        setIsLoading(false)
                        // HUtils.runFuntion(fnList, "hideModal", [])
                      },
                      cbError: e => {
                        console.warn("cbErrro:", e)
                        setIsLoading(false)
                      },
                    },
                  ])
                }
              }
            },
            getClientOptions: () => {
              return FlowClientBuildOptions({
                configHeader: {
                  sticky: true,
                },
                configTable: {
                  canDelete: false,
                  optionsTable: {
                    sizePerPage: 10000,
                    hideSizePerPage: true,
                    paginationShowsTotal: false,
                  },
                },
              })
            },
            getClientList: () => {
              return {
                Data: dataList,
                // Data: [
                //   { Id: "1", Key: "v", Value: "vv" },
                //   { Id: "2", Key: "v", Value: "vv" },
                // ],
                ExtraData: {
                  Columns: {
                    Id: { FieldName: "Id", CanShow: true },
                    Key: {
                      FieldName: "Key",
                      CanShow: true,
                      CanSort: false,
                      Width: "30%",
                      Type: {
                        type: "readonly_with_tree",
                        more: JSON.stringify({
                          fLevel: "TreeLevel",
                          fLastOfLevel: "LastOfLevel",
                          fHasChild: "HasChild",
                          fLevelConfig: "LevelConfigs",
                        }),
                      },
                    },
                    Value: {
                      FieldName: "Value",
                      CanShow: true,
                      CanSort: false,
                      CanEdit: canEdit ? true : false,
                      Width: "70%",
                      Type: { type: "superman" },
                    },
                  },
                },
              }
            },
          }}
        />
      </div>
      <div style={{ borderTop: "1px solid #e9ecef" }}></div>
      {isLoading && (
        <div
          style={{
            position: "absolute",
            left: 0,
            top: 0,
            width: "100%",
            height: "100%",
            background: "#ffffff99",
          }}
        ></div>
      )}
    </WrapJsonModalContent>
  )
}
export default function MTCJsonEditor(props) {
  const ctc = useConfigTableCell({ props })
  let _obj = null
  if (typeof ctc.valueCell === "string" && ctc.valueCell.length > 0) {
    _obj = HJson.getObj(ctc.valueCell)
  }
  const config = {
    ...ctc.getSharedConfig(),
    ...ctc.moreObj,
    jsonview: {
      showMini: true,
      enableClipboard: false,
      displayDataTypes: false,
      ...ctc.moreObj.jsonview,
    },
  }
  // console.warn("render json_editor:", _obj)
  return (
    <div
      className={ctc.classNameWrap("mtc-json_editor")}
      onClick={() => {
        console.warn("Click json:", props)
        MyModal.showFromComponent(
          props => {
            return (
              <ModalEditor
                {...props}
                ctc={ctc}
                obj={_obj || {}}
                config={config}
              />
            )
          },
          {
            title: fnListMyTableCell.fnGetHeaderTitle(ctc.fnList) || "JSON",
            backdrop: "static",
            showHeader: false,
            fnList: {
              updateCell: (v, opts) => {
                console.warn("Updatecell:", v, opts)
                ctc.updateFieldData(v, opts)
              },
            },
          }
        )
      }}
      style={{
        // maxHeight: "120px",
        cursor: ctc.canEdit === true ? "pointer" : "inherit",
        ...config.style,
      }}
    >
      {_obj != null && config.jsonview.showMini === true && (
        <JsonContainerMini>
          <div>JSON ({_obj != null && `${Object.keys(_obj).length}`})</div>
          <div style={{ color: "grey", fontWeight: "normal" }}>
            {ctc.valueCell}
          </div>
        </JsonContainerMini>
      )}
      {_obj == null && config.jsonview.showMini === true && (
        <JsonContainerMini style={{ color: "grey" }}>
          <div>JSON</div>
        </JsonContainerMini>
      )}
      {_obj != null && config.jsonview.showMini !== true && (
        <JsonContainer>
          <ReactJson src={_obj} {...config.jsonview} />
        </JsonContainer>
      )}
    </div>
  )
}
